import LookupField from './lookup-field/lookup-field';
import MultiLookup from './multi-lookup/multi-lookup';
import Button from './button/Button';
import Dropdown from './dropdown/dropdown';
import ButtonDropDown from './button-dropdown/button-dropdown';
import ButtonBarSelector from './button-bar-selector/button-bar-selector';
import Toggle from './toggle/toggle';
import TimeRangePicker from './time-range-picker/time-range-picker';
import Textarea from './textarea/textarea'

if (window.LMUI) {
    LMUI.LookupField = LookupField;
    LMUI.MultiLookup = MultiLookup;
    LMUI.Button = Button;
    LMUI.Dropdown = Dropdown;
    LMUI.ButtonDropdown = ButtonDropDown;
    LMUI.Toggle = Toggle;
    LMUI.TimeRangePicker = TimeRangePicker;
    LMUI.ButtonBarSelector = ButtonBarSelector;
    LMUI.Textarea = Textarea;
} else if (typeof exports != 'undefined') {
    exports.LookupField = LookupField;
    exports.MultiLookup = MultiLookup;
    exports.Button = Button;
    exports.Dropdown = Dropdown;
    exports.ButtonDropdown = ButtonDropDown;
    exports.Toggle = Toggle;
    exports.TimeRangePicker = TimeRangePicker;
    exports.ButtonBarSelector = ButtonBarSelector;
    exports.Textarea = Textarea;
}




